






































import Vue from 'vue';

export default Vue.extend({
    name: 'Login',
    data: () => {
        return {
            isRegistering: false,

            email: '',
            password: '',

        }
    },
    methods : {
        login: function () {
            let email = this.email;
            let password = this.password;
            this.$store.dispatch('login', { email, password })
                .then(() => this.$router.push('/'))
                .catch(err => console.log(err))
        }
    }
});

